  }

  .menu__list--open {
    animation: opacify;
    animation-duration: 0.15s;
    animation-delay: 0.15s;

    animation-fill-mode: both;
  }

  .menu__list--close {
    animation: opacify;
    animation-duration: 0.15s;
    animation-direction: reverse;

    animation-fill-mode: both;
  }

  .menu__list--visible {
    display: block;
  }

  .menu__list--no-js {
    position: static;

    display: block;
    width: auto;
    height: auto;
    padding: 0;

    color: #ffffff;
  }

  .menu__list::before {
    content: "";
    position: absolute;
    top: 6px;
    right: 0;
    z-index: -1;

    display: none;
