    width: 42px;
    height: 42px;

    background-color: #ffffff;
    border-radius: 50%;
    transform: scale(12);
  }

  .menu__list--visible::before {
    display: block;
  }

  .menu__list--open::before {
    animation-name: bubble;
    animation-duration: 0.3s;

    animation-fill-mode: both;
  }

  .menu__list--close::before {
    animation-name: bubble;
    animation-duration: 0.3s;
    animation-direction: reverse;

    animation-fill-mode: both;
